import React from "react";
import PropTypes from "prop-types";
import { Link } from "gatsby";

import Nav from "./Nav";

const Header = ({ title }) => {

 const [toggleNav, setToggleNav] = React.useState(false)

	return (

	<div className={`${toggleNav ? `site-head-open` : ``}`} >
		<header id="header" className="header site-head" role="banner">

				<div className="container site-head-container">

      <div className="main-bar">

								<Link to="/" className="logo">
								 <img src={'/images/logo-small.png'} />&nbsp;{title}
								</Link>

								<a className="nav-burger" href={`#`} onClick={() => setToggleNav(!toggleNav)}>
										<div className="hamburger hamburger--collapse"
															aria-label="Menu"
               role="button"
               aria-controls="navigation">
												<div className="hamburger-box">
														<div className="hamburger-inner" />
												</div>
										</div>
								</a>

      </div>

						<Nav />

				</div>

		</header>
		</div>
	);
};

Header.propTypes = {
	title: PropTypes.string,
};

export default Header;
